import {menuUser} from '@/api/public.js'
import {user} from '@/api/user.js'
import {mapState} from "vuex"
export default {
	data() {
		return {
			list:[
				{id:1,pic:"",name:"配送地址"},
				{id:2,pic:"",name:"优惠券"}
			],
			user:{}
		}
	},
	computed:{
		...mapState(["token"])
	},
	methods: {
		toMyCoupon(){
			uni.navigateTo({
				url:'/pages/myCoupon/myCoupon'
			})
		},
		click(val){
			console.log(val)
			switch (val){
				case "配送地址":
				uni.navigateTo({
					url:'/pages/address/address'
				})
				break;
				case "优惠券":
				uni.navigateTo({
					url:'/pages/coupon/coupon'
				})
				break;
				case "签到有礼":
				uni.navigateTo({
					url:'/pages/sign/sign'
				})
				break;
				default:console.log("错误")
				break;
			}
		}
	},
	onShow() {
		console.log(121)
		if(this.token==""){
			uni.navigateTo({
				url:"../login/login"
			})
		}else{
			user().then(data=>{
				this.user=data.data.data
			})
		}
		
		
		
		// menuUser().then(data=>{
		// 	console.log(data)
		// 	this.list=data.data.data.routine_my_menus
		// })
		
	}
}
